import {useEffect, useState} from "react";
import {Button, Col, Row} from "react-bootstrap";
import {Card} from "react-bootstrap";
import React from "react";

function GetRemindList() {
    const [remind, setRemind] = useState({});
    const [range, setRange] = useState('A2:L1000');
    const [isLoading, setIsLoading] = useState(false);
    const [isError, setIsError] = useState(false);
    const [sheetId] = useState('1xyVAC8Rby3n_s5NERM2_8uPYic11YlkycDM_TknOgTs');

    useEffect(() => {
        const fetchData  = () =>{
            fetch('https://sheets.googleapis.com/v4/spreadsheets/' + sheetId + '/values/' + range + '?key=AIzaSyAYfx0INy81MTWwUDbGb1iPN88g8_lnoDc',
                {
                    method: "GET",
                    headers: new Headers({
                        'Content-Type': 'application/json',
                    })
                })
                .then(res => res.json())
                .then(data => {
                    setIsLoading(false);
                    setRemind(data.values);
                    console.log(data);
                })
                .catch(e => {
                    setIsLoading(false);
                    setIsError(true);
                    console.error(e);
                });
        };
        fetchData();
    }, []);
    if (isLoading) {
        return <div>Loading...</div>;
    }
    if (isError) {
        return <div>Error</div>;
    }
    if(remind.length === 0){
        return <div>No any remind</div>;
    }
    return(
        <Row>
            {remind.length > 0 &&remind.map((obj, i) =>(
                <Col md={6} sm={12} className="mb-2" key={i}>
                    <Card className="bg-white border-white border-0 shadow-sm">
                        <div className="card-custom-img" />
                        <div className="card-custom-avatar">
                            <img className="img-fluid"/>
                        </div>
                        <Card.Body>
                            <Row>
                                <Col></Col>
                                <Col>{obj[0]}</Col>
                            </Row>
                            <Row>
                                <Col></Col>
                                <Col>{obj[3]}</Col>
                            </Row>
                            <Row>
                                <Col></Col>
                                <Col>{obj[4]}</Col>
                            </Row>
                            <Row>
                                <Col></Col>
                                <Col>{obj[5]}</Col>
                            </Row>
                            <Row>
                                <Col></Col>
                                <Col>{obj[6]}</Col>
                            </Row>
                            <Row>
                                <Col></Col>
                                <Col>{obj[7]} {obj[8]} {obj[9]}</Col>
                            </Row>
                        </Card.Body>
                        <Card.Footer style={{background: 'inherit', borderColor: 'inherit'}}>
                            <Row>
                                <Col className="d-grid gap-2">
                                    <Button variant="warning" className="rounded-pill"></Button>
                                </Col>
                                <Col className="d-grid gap-2">
                                    <Button variant="danger" className="rounded-pill"></Button>
                                </Col>
                            </Row>
                        </Card.Footer>
                    </Card>
                </Col>
            ))}
        </Row>
    );
}

const Home = () => {
    return (
        <div>Home</div>
    );
};

export default Home;