import React, { useEffect, useState } from 'react';
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
//import { useNavigate } from "react-router-dom";
import { useLiff } from 'react-liff';

import logo from './logo.svg';
import './App.css';

import Home from "./pages/Home";

/*<Container>
  <GetRemindList></GetRemindList>
  <AddRemind></AddRemind>
</Container>*/

function App() {
    //let navigate = useNavigate();
    const [profile, setProfile] = useState({
        "userId": "",
        "displayName": "",
        "pictureUrl": "",
        "statusMessage": "",
    });
    const { error, liff, isLoggedIn, ready } = useLiff();

    useEffect(() => {
        if (!isLoggedIn) return;
        (async () => {
            const profile = await liff.getProfile();
            setProfile({
                "userId": profile.userId,
                "displayName": profile.displayName,
                "pictureUrl": profile.pictureUrl,
                "statusMessage": profile.statusMessage,
            });
        })();
    }, [liff, isLoggedIn]);

    const showDisplayName = () => {
        if (error) return <p>Something is wrong.</p>;
        if (!ready) return <p>Loading...</p>;

        if (!isLoggedIn) {
            return <button className="App-button" onClick={liff.login}>Login</button>;
        }
        if(isLoggedIn){
            return (
                <div>Welcome , {profile.displayName}!</div>
            );
        }
        return (
            <>
              <p>Welcome , {profile.displayName}!</p>
              <button className="App-button" onClick={liff.logout}>Logout</button>
            </>
        );
    };
  return (
      <Router>
          <div className="App">
              <header className="App-header">
                  <img src={logo} className="App-logo" alt="logo" />
                  {showDisplayName()}
              </header>
          </div>
          <Routes>
            <Route path="/Home" element={<Home/>}/>
          </Routes>
      </Router>
  );
}

export default App;
